import MasterPage from "../components/Master";
import fetch from "isomorphic-unfetch";
import Precio from "../components/Precio";
import Noticias from "../components/Noticias";

const Index = props => (
  <MasterPage>
    <div className="row">
      <div className="col-12">
        <h2>Precio del bitcoin</h2>
        <Precio precio={props.precioBitcoin} />
      </div>
      <div className="col-md-8">
        <h2>Noticias sobre bitcoin</h2>
        {console.log(props.noticias)}
        <Noticias noticias={props.noticias} />
      </div>
      <div className="col-md-4">
        {console.log(props.eventos)}
        <h2>Proximos eventos bitcoin</h2>
      </div>
    </div>
  </MasterPage>
);

Index.getInitialProps = async () => {
  const noticias = await fetch(
    "https://newsapi.org/v2/everything?q=bitcoin&from=2019-08-06&sortBy=publishedAt&apiKey=96d26c4e5baa4d6185535bd66f8bdef9&language=es"
  );
  const eventos = await fetch(
    "https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Colombia&token=K7JJEVR6YVY5CPBOSVXS"
  );
  const key = "e1f35cdd-c426-4451-b546-e44afb47d641";
  var objHeader = {
    headers: {
      "X-CMC_PRO_API_KEY": key,
      "Accept-Encoding": "deflate"
    },
    json: true,
    gzip: true
  };

  const precio = await fetch(
    "https://pro-api.coinmarketcap.com/v1/cryptocurrency/quotes/latest?symbol=BTC",
    objHeader
  );
  const resPrecio = await precio.json();
  const resNoticias = await noticias.json();
  const resEventos = await eventos.json();

  return {
    precioBitcoin: resPrecio.data.BTC.quote.USD,
    noticias: resNoticias.articles,
    eventos: resEventos
  };
};
export default Index;
